import React from 'react';

const UserDetail = props => {
    const users = props.user;

    return (
        <div className="userDetailWrapper">
            <ul>
                <li className="clearfix">
                    <div className="keys">Name</div>
                    <div className="values">{users.firstName} {users.lastName}</div>
                </li>
                <li className="clearfix">
                    <div className="keys">Email</div>
                    <div className="values">{users.email}</div>
                </li>
                <li className="clearfix">
                    <div className="keys">Contact</div>
                    <div className="values">{users.phone}</div>
                </li>
                <li className="clearfix">
                    <div className="keys">Address</div>
                    <div className="values">{users.address.city}, {users.address.state}, {users.address.country}</div>
                </li>
            </ul>
            <button onClick={props.handleClick}>Back</button>
        </div>
    )
}

export default UserDetail;