import React, { Component } from 'react';
import EnhancedUsers from './hoc/EnhancedUsers';

class App extends Component {
  constructor() {
    super();

    this.state = {
      isLoaded: false,
      users: [],
      user:null,
      selectedUserId: null
    }
  }

  async componentDidMount() {
    const users = await fetch('https://mock-io.herokuapp.com/users');
    const userData = await users.json();
    this.setState({
      isLoaded: true,
      users: userData
    }, () => {
      console.log(this.state);
    })
  }

  handleClick=(e)=>{
    if(e.target.tagName === 'P'){
      this.setState({
        user: this.state.users[e.target.id - 1],
        selectedUserId: e.target.id
      })
    }else{
      this.setState({
        selectedUserId: null,
        users: this.state.users
      })
    }
    
  }

  render() {
    return (
      !this.state.isLoaded ?
        <div className="loader"></div>
        :
        <EnhancedUsers        
          users={this.state.users}
          user={this.state.user}
          selectedindex={this.state.selectedUserId}
          handleClick={this.handleClick}
        />
    );
  }
}

export default App;
